<template>
  <div id="app" class="text-center">
    <h1>Word translator</h1>
    <h5>Powered by Vue.js and Yandex API</h5>
    <div id="app-inner">
      <translate-form class="col" v-on:formSubmit="translateText"></translate-form> 
      <div id="arrow" class="col">
        <i class="ion-chevron-right"></i>
      </div>
      <translate-out class="col" id="translated" v-text="translatedText"></translate-out>
    </div>
  </div>
</template>

<script>
import TranslateForm from './components/TranslateForm.vue'
import TranslateOut from './components/TranslateOut.vue'
export default {
  name: 'app',
  data () {
   return {
      translatedText: ''
   }
  },
  components: {
    TranslateForm,
    TranslateOut
  },
  methods: {
    translateText(text, language)  {
      if(text) {
        this.$http.get('https://translate.yandex.net/api/v1.5/tr.json/translate?&key=trnsl.1.1.20171130T163455Z.499fa42a8728e181.99fd356e7ae5579c64b423e0020bbc0ec8b39098&lang=' + language + '&text='+text)
      .then((response) => {
        this.translatedText = response.body.text[0];
      });
      } else {
        text = ''
      }
    }
  }
}
</script>

<style>
#app-inner {
  display: flex;
}
.col {
  width: 49%;
  margin: 20px;
}
#translated {
  min-height: 200px;
  max-height: 300px;
}
textarea {
  font-size: 22px;
}
#arrow {
  width: 2%;
  line-height: 200px;
  font-size: 40px;
  margin: 0;
}
</style>



