<template>
    <div id="translate-form-main">
        <form class="form-group" action="#">
            <textarea class="form-control" v-on:keyup="formSubmit" placeholder="Type text..." v-model="textToTranslate" ></textarea>
            <select class="form-control" v-on:click="formSubmit" v-model="language">
                <option value="ru">Русский</option>
                <option value="en">English</option>
                <option value="uk">Украйиньська</option>
                <option value="pl">Polski</option>
            </select>
            <button class="btn btn-success">Translate</button>
        </form>
    </div>
</template>

<script>
    export default {
        name: 'translate-form',
        data() {
            return {
                textToTranslate: ''
            }
        },
        created() {
            this.language = 'ru'
        },
        methods: {
            formSubmit() {
                this.$emit('formSubmit', this.textToTranslate, this.language);
            }
        }
    }
</script>

<style>
 textarea.form-control {
     resize: none;
     width: 100%;
     height: 200px;
 }
</style>


